import Default from './default'
// emoji
import Emoji from './emoji'
// lxh
import Lxh from './lxh'

//  (default + lxh)
let emoticons = {}

// default
fetchEmoticons(Default.emoticons, 'emoticons/default/', '@3x')
// 
fetchEmoticons(Lxh.emoticons, 'emoticons/lxh/', '@2x')
// 
function fetchEmoticons(emos, relativePath, scale) {
  // default
  for (let i = 0; i < emos.length; i++) {
    const emo = emos[i]
    //  key-value
    let png = emo.png
    let key = emo.chs
    // key or png 
    if (png.length !== 0 && key.length !== 0) {
      //  public
      // - [public ](https://cli.vuejs.org/zh/guide/html-and-static-assets.html#public-%E6%96%87%E4%BB%B6%E5%A4%B9)
      // - [vue-cli 3.0  public ](http://www.cnblogs.com/cckui/p/10315204.html)
      // - [vue-cli3static assets](https://segmentfault.com/a/1190000014456796?utm_source=index-hottest)
      // - [vue-cli  assetsstatic --save-dev--save](https://juejin.im/post/59be4d325188257e764c8485)
      // - [vue src](https://blog.csdn.net/Mr_YanYan/article/details/78783091)
      // - [Vueimgsrcstatic](https://www.jianshu.com/p/f82c5ecbd3a5)
      // - [vue+webpacksrc404](https://segmentfault.com/q/1010000006743502)
      // - [vue](https://www.jianshu.com/p/fab484498e4e)
      // relativePath <PS:  /public/xxx>
      //  @3x @2x @1x 
      //  JS
      // - [JS](https://www.cnblogs.com/FallIntoDarkness/p/9757334.html)
      let spl = png.split('.')
      let fileName = spl[0]
      let suffix = spl[1]
      
      // public 
      //  public'/emoticons/default/d_aini@3x.png' build
      // - [Vue CLI 3 ](https://segmentfault.com/a/1190000016120011)
      // - [vue-cli](https://www.cnblogs.com/zly1022/p/10108548.html)
      // - [[Vue CLI 3] public ](https://segmentfault.com/a/1190000016414534)
      // - [vue-cli](https://www.cnblogs.com/yszblog/p/10239712.html)
      let path = process.env.BASE_URL + relativePath + fileName + scale + '.' + suffix

      console.log('baseUrl ---  ' + process.env.BASE_URL);

      //  JSkey
      // - [JSkey](https://blog.csdn.net/wml00000/article/details/85334850)
      // - [JS--](https://www.cnblogs.com/bigberg/p/9237856.html)
      // - [js](https://blog.csdn.net/ganyingxie123456/article/details/78163154)
      // - [Javascript](https://blog.csdn.net/limlimlim/article/details/9088161)
      emoticons[key] = path
    }
  }
}

//  
// - [JS](https://blog.csdn.net/yu17310133443/article/details/53183930)
// PS why?
function imgIsExist(path) {
  var ImgObj = new Image()
  ImgObj.src = path
  if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
    return true
  } else {
    return false
  }
}

export default emoticons
