<template>
  <div class="mh-more__wrapper" @touchstart.stop>
    <div class="mh-more__thumb" @click="thumbAction">
      <!--  :css="false"  -->
      <transition
        @before-enter="beforeEnter"
        @enter="enter"
        @after-enter="afterEnter"
      >
        <img
          v-if="show"
          src="@/assets/images/moments/wx_albumLikeHL_20x20.png"
          class="mh-more__ani-pic"
        />
      </transition>
      {{ liked ? "" : "" }}
    </div>
    <div class="mh-more__line"></div>
    <div class="mh-more__comment" @click="commentAction"></div>
  </div>
</template>

<script>
export default {
  name: "MomentOperationMore",
  props: {
    thumbed: Number
  },
  data() {
    return {
      liked: this.thumbed > 0 ? true : false,
      show: false
    };
  },
  methods: {
    thumbAction() {
      this.liked = !this.liked;
      this.show = true;
      // 
      setTimeout(() => {
        // 
        this.$emit("thumb-click", this.liked ? 1 : 0);
      }, 250);
    },
    commentAction() {
      // 
      setTimeout(() => {
        // 
        this.$emit("comment-click");
      }, 250);
    },
    //  el DOM JS DOM
    //   el  document.getElementById('') JS DOM
    beforeEnter(el) {
      // beforeEnter   beforeEnter 
      // 
      el.style.transform = "scale(.5, .5)";
    },
    enter(el, done) {
      // 
      //  el.offsetWidth 
      el.offsetWidth;
      // enter  
      el.style.transform = "scale(2.5, 2.5)";
      el.style.transition = "transform .25s ease-in-out";
      //  done  afterEnter done  afterEnter 
      done();
    },
    afterEnter() {
      //  afterEnter
      this.show = false;
    }
  }
};
</script>

