//    or Methods

// 
import { blackBackItem } from '../js/MHBarButtonItem'

const utils = {
  install(Vue) {
    // 
    Vue.prototype.blackBackItem = blackBackItem
  },

  // 
  // - [JavaScript ](http://www.runoob.com/w3cnote/javascript-check-arrayisobject.html)
  // - [JS](https://www.cnblogs.com/heshan1992/p/6927690.html)
  objIsArray(obj) {
    if (typeof Array.isArray === 'function') {
      return Array.isArray(obj)
    } else {
      return Object.prototype.toString.call(obj) === '[object Array]'
    }
  },

  // 
  // - [js](https://blog.csdn.net/astonishqft/article/details/88559176)
  isObject (arg) {
    return Object.prototype.toString.call(arg) === '[object Object]'
  },

  // 
  // - [JS](https://www.cnblogs.com/baiyangyuanzi/p/6726258.html)
  // - [JavaScript12](https://segmentfault.com/a/1190000016418021)
  uniqueArray(arr) {
    return Array.from(new Set(arr))
  },

  // QQQQ
  // - [qq,](https://blog.csdn.net/qq_29091239/article/details/80075981)
  // - [](https://blog.csdn.net/unknowna/article/details/50524529)
  validQQ(qq){
    let regex = /^[1-9][0-9]{4,9}$/g
    return regex.test(qq)
  },

  validWeChatId(id){
    let regex = /^[a-zA-Z]{1}[-_a-zA-Z0-9]{5,19}$/g
    return regex.test(id)
  },

  validQQMail (mail){
    let regex = /^[1-9][0-9]{4,9}@qq\.com$/g
    return regex.test(mail)
  },

  // 
  // - [](https://github.com/VincentSit/ChinaMobilePhoneNumberRegex)
  validMobile(mobile) {
    let regex = /((?:\+?86)?1(?:3\d{3}|5[^4\D]\d{2}|8\d{3}|7(?:[35678]\d{2}|4(?:0\d|1[0-2]|9\d))|9[189]\d{2}|66\d{2})\d{6})+?/g
    return regex.test(mobile)
  },

  // 344
  // - [](https://blog.csdn.net/Wangdanting123/article/details/86938915)
  // - [](https://segmentfault.com/q/1010000004508861)
  formatMobile344(mobile) {
    return (mobile + '').replace(/(^\d{3}|\d{4}\B)/g, '$1 ')
  },

  // 
  removesWhitespace(str) {
    return (str || '').replace(/\s/g, "")
  },
  // 
  includeChinese(str) {
    var patrn = /[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/gi
    if (!patrn.exec(str)) {
      return false
    } else {
      return true
    }
  },

  //  ^[0-9]*$
  pureDigitCharacters (str) {
    let regex = /^[0-9]*$/gi
    return regex.test(str)
  }
}

// 
export default utils
