<template>
  <div class="_full-container">
    <div class="_full-content _content-padding-top44">
      <!--  -->
      <NavigationBar
        :left-item="leftItem"
        @left-click="$router.back()"
        :right-item="rightItem"
        @right-click="navRightItemAction"
        title=""
      ></NavigationBar>
      <!-- language -->
      <div class="weui-cells weui-cells_radio">
        <label
          class="weui-cell weui-check__label"
          v-for="(lan, index) in languages"
          :key="index"
        >
          <div class="weui-cell__bd">
            <p>{{ lan }}</p>
          </div>
          <div class="weui-cell__ft">
            <input
              type="radio"
              class="weui-check"
              id="lan"
              :value="lan"
              v-model="language"
            />
            <span class="weui-icon-checked"></span>
          </div>
        </label>
      </div>
    </div>
  </div>
</template>

<script>
import MHBarButtonItem from "@/assets/js/MHBarButtonItem.js";
export default {
  name: "language-picker",
  components: {},
  data() {
    return {
      languages: [],
      language: "",
      // ,true
      completed: false,
      leftItem: new MHBarButtonItem("", "", 0),
      rightItem: new MHBarButtonItem("", "", 0)
    };
  },
  created() {
    console.log(this.$route.params);
    this.language = this.$route.params.language;
    this.configTitles();
  },
  activated() {},
  methods: {
    configTitles() {
      /// https://www.zybang.com/question/8b478510701184609e88eec8d869f87e.html  
      let titles = [
        "",
        "",
        "",
        "English",
        "Bahasa Indonesia",
        "Bahasa Melayu",
        "espaol",
        "",
        "Italiano",
        "",
        "Polski",
        "Portugus",
        "",
        "",
        "Ting Vit",
        "",
        "",
        "",
        "Trke",
        "Deutsch",
        "Franais"
      ];
      this.languages = titles;
    },
    navRightItemAction() {
      this.completed = true;
      // 
      this.$router.back();
    }
  },
  // 
  beforeRouteLeave(to, from, next) {
    // 
    //  `this`
    if (this.completed) {
      //  - [](https://www.cnblogs.com/buerjj/archive/2017/11/26/7900696.html)
      to.params.language = this.language;
    }
    // next
    next();
  }
};
</script>

<style scoped>
.weui-cells {
  margin-top: 0;
  line-height: 1;
}
</style>
